<template>
  <div class="content">
    <roomInfo :data="this.getRespone.info" />
    <changeSpecialRoom :active='active' @changeAcitve='operationBtn()'/>
    <changeSpaRoom :active='active' @changeAcitve='operationBtn()'/>
    <changeRestRoom :active='active' @changeAcitve='operationBtn()'/>
    <section class="operationMean" v-show="active == null">
      <div
        class="mean"
        v-for="(item,index) in this.getMeanJson.operationBtn"
        :key="index"
        @click="operationBtn(index)"
      >
        <img :src="item.imgIco" />
        <p>{{item.name}}</p>
      </div>
    </section>
  </div>
</template>

<script>
import roomInfo from "@/components/roomInfo";
import changeSpecialRoom from '@/components/freeForm/changeSpecialRoom'
import changeRestRoom from '@/components/freeForm/changeRestRoom'
import changeSpaRoom from '@/components/freeForm/changeSpaRoom'

export default {
  data() {
    return {
      active: null
    };
  },
  methods: {
    operationBtn(id) {
      if(id != undefined){
        this.active = id;
      }else{
        this.active = null;
      }
    },
    close(type) {
      this.show[type] = false;
    }
  },
  components: {
    roomInfo,
    changeSpecialRoom,
    changeRestRoom,
    changeSpaRoom
  }
};
</script>

<style lang="scss" scoped>
.content {
  background: $F5;
  overflow: hidden;
  position: relative;
}
.operationMean {
  @include grid(3, auto);
  @include sc(0.9rem, $fColor);
  @include wh(100%, auto);
  position: fixed;
  bottom: 0rem;
  z-index: 2;
  background-color: $fc;
  .mean {
    background-color: $fc;
    padding: 0.5rem;
    img {
      @include wh(2.5rem, 2.5rem);
      border: 0.2rem solid $F5;
      border-radius: 5rem;
    }
  }
}
</style>